.header {
  font-family: 'Pacifico', cursive;
  color: rgb(0, 0, 197);
  text-align: center;
  text-shadow: 1px 1px yellow,
    1px -1px yellow,
    -1px -1px yellow,
    -1px 1px yellow;
  background: lightblue;
  box-shadow: inset 0px -1px 5px magenta,
    0px 1px 5px magenta;
  margin: 0 auto 2px auto;
  padding: 15px 0 15px 0;
}
.instructions {
  font-family: 'Pacifico', cursive;
  color: rgb(0, 0, 170);
  text-align: justify;
  background: lightblue;
  border-radius: 5px;
  box-shadow: inset 0px -1px 3px magenta,
  inset -1px 0px 3px magenta,
  inset 0px 1px 3px magenta,
  inset -1px 0px 3px magenta,
    0px 1px 3px magenta,
    0px -1px 3px magenta,
    -1px 0px 3px magenta,
    1px 0px 3px magenta;
  margin: 5px auto 10px auto;
  padding: 15px;  
  width: 80%;
}
.card {
  background: white;
  border: 2px solid yellow;
  border-radius: 100px;
  box-shadow: 1px 1px 5px yellow,
    1px -1px 5px yellow,
    -1px -1px 5px yellow,
    -1px 1px 5px yellow,
    inset 1px 1px 5px blue,
    inset -1px 1px 5px blue,
    inset -1px -1px 5px blue,
    inset 1px -1px 5px blue;
  margin: 2% 1% 2% 4%;
  float: left;
  width: 200px;
  height: 200px;
  cursor: -webkit-grab;
  cursor: grab;
}
.card :active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.imageBox {
  margin: 3%;
  height: 94%;
  width: 94%;
}
.image {
  width: 99%;
  height: 99%;
  border-radius: 100px;
}
.container {
  width: 90%;
  margin: 0px auto 0px auto;
}
.scoreboard {
  width: 420px;
  margin: 0px auto 0px auto;
  font-family: 'Pacifico', cursive;
}
.score {
  float: left;
  font-size: 130%;
  text-align: center;
  background: lime;
  border-radius: 20px;
  box-shadow: inset 1px 1px 5px blue,
    inset -1px 1px 5px blue,
    inset 1px -1px 5px blue,
    inset -1px -1px 5px blue;
  width: 200px;
  margin: 5px;
  padding: 5px 0 5px 0;
}
.cheer {
  color: rgb(0, 0, 170);
  text-decoration: underline;
  font-size: 130%;
  text-align: center;
  background: lightblue;
  border-radius: 25px;
  box-shadow: 1px 1px 3px magenta,
  1px -1px 3px magenta,
  -1px -1px 3px magenta,
  -1px 1px 3px magenta;
  margin: 7px auto 6px auto;
  width: 80%;
}
.body {
  background: url(/MemoryGame/static/media/background.97a9f261.jpg) no-repeat;
  background-size: 100% 100%;
}

